import http from '@/components/utils/http.js';
export default {
	data() {
		return {
			server:this.apiServer,
			title: '产品分类',
			sizeCalcState: false,
			tabScrollTop: 0,
			currentId: 1,
			flist: [],
			slist: [],
			tlist: []
		};
	},
	onLoad() {
		
		if (global.islogon() == false) {
			uni.navigateTo({
				url: '../person/logon'
			});
		}
		this.loadData();
	},
	methods: {
		loadData() {
			
			let opts = {
				url: '/productApi/cateList/',
				method: 'post'
			};
			let param = {};
			http.httpTokenRequest(opts, param).then(
				res => {
					// console.log(res.data);
					//打印请求返回的数据
					if (res.data['code'] == 0) {
						console.log('list', res.data['cateList']);
						this._getdata(res.data['cateList']);
					} else {
						uni.showToast({ title: res.data['msg'], icon: 'none' });
					}
				},
				error => {
					console.log(error);
				}
			);
		},
		async _getdata(cateList) {
			let list = cateList;

			list.forEach(item => {
				if (!item.pid) {
					this.flist.push(item); //pid为父级id, 没有pid或者pid=0是一级分类
				} else if (!item.picture) {
					this.slist.push(item); //没有图的是2级分类
				} else {
					this.tlist.push(item); //3级分类
				}
			});
			console.log('list', this.flist);
		},
		//一级分类点击
		tabtap(item) {
			if (!this.sizeCalcState) {
				this.calcSize();
			}

			this.currentId = item.id;
			let index = this.slist.findIndex(sitem => sitem.pid === item.id);
			this.tabScrollTop = this.slist[index].top;
		},
		//右侧栏滚动

		asideScroll(e) {
			if (!this.sizeCalcState) {
				this.calcSize();
			}
			let scrollTop = e.detail.scrollTop;
			let tabs = this.slist.filter(item => item.top <= scrollTop).reverse();
			if (tabs.length > 0) {
				this.currentId = tabs[0].pid;
			}
		},
		//计算右侧栏每个tab的高度等信息
		calcSize() {
			let h = 0;
			this.slist.forEach(item => {
				let view = uni.createSelectorQuery().select('#main-' + item.id);
				view.fields(
					{
						size: true
					},
					data => {
						item.top = h;
						h += data.height;
						item.bottom = h;
					}
				).exec();
			});
			this.sizeCalcState = true;
		},
		navToList(sid, tid) {
			uni.navigateTo({
				url: `/pages/product/list?fid=${this.currentId}&sid=${sid}&tid=${tid}`
			});
		}
	}
};




